import { KeyboardEventHandler } from "react"
import { Control, Controller } from "react-hook-form"
import { Box, ToggleButton } from "@rp/react/web/mui/core"
import { FormValues, TIME_RANGES } from "./WatchlistSmartGridModal.utils"

type Props = { control: Control<FormValues>; onKeyDown?: KeyboardEventHandler<HTMLButtonElement> }

function TimeRangeSelector({ control, onKeyDown }: Props) {
  return (
    <Controller
      control={control}
      name="timeRange"
      render={({ field }) => {
        const { value, onChange } = field

        return (
          <Box
            sx={{
              display: "flex",
              gap: 1,
              flexWrap: "wrap",
              "& button.MuiButtonBase-root": {
                ml: 0,
                color: "text.secondary",
                fontSize: 16,
                borderRadius: 6,
                height: 40,
                width: "170px",
                textTransform: "none",
              },
            }}
          >
            {TIME_RANGES.map(label => (
              <ToggleButton
                key={label}
                value={label}
                selected={value === label}
                onClick={() => onChange(value === label ? null : label)}
                onKeyDown={onKeyDown}
              >
                {label}
              </ToggleButton>
            ))}
          </Box>
        )
      }}
    />
  )
}

export { TimeRangeSelector }
